import { FC, useMemo } from 'react';
import classNames from 'classnames';
import Markdown from 'react-markdown';

// Styles
import './MarkdownRenderer.scss';

// Types
import { TestMetadata } from 'types';

export interface MarkdownRendererProps {
  markdown: string;
  className?: string;
  // Test IDs for component sections
  testMetadata?: {
    markdownRenderer?: TestMetadata;
    markdownContent?: TestMetadata;
  };
}

const MarkdownRenderer: FC<MarkdownRendererProps> = ({
  markdown,
  className,
  testMetadata = {
    markdownRenderer: { 'data-testid': 'markdown' },
  },
  ...otherProps
}) => {
  const markdownClassNames = useMemo(
    () => classNames('sb-markdown', className),
    [className],
  );
  return (
    <div
      {...otherProps}
      className={markdownClassNames}
      {...testMetadata?.markdownRenderer}
    >
      <div className="sb-markdown__content" {...testMetadata?.markdownContent}>
        <Markdown>{markdown}</Markdown>
      </div>
    </div>
  );
};

export default MarkdownRenderer;
