import { FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './Skeleton.scss';

// Types
import { TestMetadata } from 'types';

export interface SkeletonProps {
  /** Skeleton width */
  width?: string | number;
  /** Class name */
  className?: string;
  /* isLoading flag to render children if false and Skeleton if true
   * by default is true
   */
  isLoading?: boolean;
  /** children to render when loading is done */
  children?: ReactNode;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const Skeleton: FC<SkeletonProps> = ({
  width = '100%',
  className,
  isLoading = true,
  children,
  testMetadata,
  ...otherProps
}) => {
  const skeletonClassNames = useMemo(
    () => classNames('sb-skeleton', className),
    [className],
  );

  return (
    <>
      {isLoading ? (
        <span
          {...otherProps}
          style={{ width }}
          className={skeletonClassNames}
          {...testMetadata}
        >
          &zwnj;
        </span>
      ) : (
        children
      )}
    </>
  );
};

export default Skeleton;
