import {
  FC,
  ReactNode,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import classNames from 'classnames';
import shortid from 'shortid';

// Styles
import './CollapsiblePanel.scss';

// Types
import { TestMetadata } from 'types';

export interface CollapsiblePanelProps {
  /** Elements to Wrap **/
  children?: ReactNode;
  /** Class name **/
  className?: string;
  /** Determines if panel is open by default when uncontrolled, ignored if `isOpen` is used **/
  isOpenByDefault?: boolean;
  /** Determines if panel is open **/
  isOpen?: boolean;
  /** Callback run when changing the open state is requested **/
  onToggleOpen?: (nextValue: boolean) => void;
  /** Content element id, will generate a random one if not provided **/
  contentId?: string;
  /** Header button element id, will generate a random one if not provided **/
  headerButtonId?: string;
  /** remove the border of the component **/
  borderless?: boolean;
  /** Element to toggle content  **/
  toggleBar?: ReactNode;
  /** Toggle bar location in relation to content - top (default), bottom  **/
  toggleBarLocation?: 'top' | 'bottom';
  /** Toggle bar clickable area */
  toggleBarClick?: 'icon' | 'full';
  /** toggleBar horizontal alignment alignment - left (default), right, center **/
  toggleHorizontalAlignment?: 'left' | 'right' | 'center';
  /** type for button on the collapsible header*/
  buttonType?: 'button' | 'submit' | 'reset';
  // Test IDs for sections of CollapsiblePanel
  testMetadata?: {
    collapsiblePanel?: TestMetadata;
    content?: TestMetadata;
  };
  /** Function that returns an element to toggle content  **/
  renderToggleBar?: (isOpen: boolean) => JSX.Element;
  /** ToggleIcon */
  toggleIcon?: string;
  /** toggleClass */
  toggleClass?: string;
  /** Selectable */
  selectable?: boolean;
  /** Selected */
  selected?: boolean;
}

const CollapsiblePanel: FC<CollapsiblePanelProps> = ({
  children,
  className,
  contentId: maybeContentId,
  headerButtonId: maybeHeaderButtonId,
  toggleBar,
  isOpen: isOpenInput,
  isOpenByDefault = false,
  borderless,
  onToggleOpen,
  toggleBarLocation = 'top',
  toggleHorizontalAlignment = 'left',
  buttonType = 'button',
  testMetadata,
  renderToggleBar = () => toggleBar,
  toggleIcon = 'chevron-right',
  toggleClass,
  toggleBarClick = 'full',
  selectable,
  selected,
  ...otherProps
}) => {
  const [isOpen, setIsOpen] = useState(
    typeof isOpenInput === 'undefined' ? isOpenByDefault : isOpenInput,
  );

  useEffect(() => {
    if (typeof isOpenInput !== 'undefined') {
      setIsOpen(isOpenInput);
    }
  }, [isOpenInput, setIsOpen]);

  const handleHeaderButtonClick = useCallback(() => {
    if (typeof isOpenInput === 'undefined') {
      setIsOpen(!isOpen);
      if (onToggleOpen) {
        onToggleOpen(!isOpen);
      }
    } else {
      if (onToggleOpen) {
        onToggleOpen(!isOpenInput);
      }
    }
  }, [setIsOpen, isOpen, isOpenInput, onToggleOpen]);

  // TODO: change to isomorphic friendly solution when available
  // https://github.com/reactjs/rfcs/pull/32
  // headerButtonId is used identify the label for the content (see aria-labelledby)
  const headerButtonId = useMemo(
    () => maybeHeaderButtonId || shortid.generate(),
    [maybeHeaderButtonId],
  );

  // contentId is used to identify that the button controls the content (see aria-controls)
  const contentId = useMemo(
    () => maybeContentId || shortid.generate(),
    [maybeContentId],
  );

  const collapsiblePanelClassNames = useMemo(
    () =>
      classNames(
        'sb-collapsible-panel',
        {
          'sb-collapsible-panel__borderless': borderless,
        },
        className,
      ),
    [className, borderless],
  );

  const collapsiblePanelTitleClassNames = useMemo(
    () =>
      classNames('sb-collapsible-panel__title', {
        'sb-collapsible-panel__title--open': isOpen,
        'sb-collapsible-panel__title--selectable': selectable,
        'sb-collapsible-panel__title--selected': selected,
        'sb-collapsible-panel__toggle-alignment-left':
          toggleHorizontalAlignment === 'left',
        'sb-collapsible-panel__toggle-alignment-center':
          toggleHorizontalAlignment === 'center',
        'sb-collapsible-panel__toggle-alignment-right':
          toggleHorizontalAlignment === 'right',
        'sb-collapsible-panel__title-clickable': toggleBarClick === 'full',
      }),
    [isOpen, toggleHorizontalAlignment, toggleBarClick, selectable, selected],
  );

  const collapsiblePanelIconClassNames = useMemo(
    () =>
      classNames(
        `fas fa-${toggleIcon} sb-collapsible-panel__icon`,
        {
          'sb-collapsible-panel__icon--down': isOpen,
        },
        toggleClass,
      ),
    [toggleIcon, isOpen, toggleClass],
  );

  const collapsiblePanelContentClassNames = useMemo(
    () =>
      classNames('sb-collapsible-panel__content', {
        'sb-collapsible-panel__content--collapsed': !isOpen,
      }),
    [isOpen],
  );

  const toggleButton =
    toggleBarClick === 'full' ? (
      <button
        className={collapsiblePanelTitleClassNames}
        onClick={handleHeaderButtonClick}
        aria-expanded={isOpen}
        aria-controls={contentId}
        id={headerButtonId}
        type={buttonType}
        // TODO: remove when isomorphic friendly solution is implemented
        suppressHydrationWarning
      >
        <div className={collapsiblePanelIconClassNames} />
        {renderToggleBar &&
          typeof renderToggleBar === 'function' &&
          toggleBarClick === 'full' &&
          renderToggleBar(isOpen)}
      </button>
    ) : (
      <div
        className={collapsiblePanelTitleClassNames}
        aria-controls={contentId}
      >
        <button
          className={classNames('sb-collapsible-panel__button', {
            'sb-collapsible-panel__button--open': isOpen,
          })}
          onClick={handleHeaderButtonClick}
          aria-expanded={isOpen}
          id={headerButtonId}
          type={buttonType}
          // TODO: remove when isomorphic friendly solution is implemented
          suppressHydrationWarning
        >
          <div className={collapsiblePanelIconClassNames} />
        </button>
        {renderToggleBar &&
          typeof renderToggleBar === 'function' &&
          toggleBarClick === 'icon' &&
          renderToggleBar(isOpen)}
      </div>
    );

  return (
    <div
      {...otherProps}
      className={collapsiblePanelClassNames}
      {...testMetadata?.collapsiblePanel}
    >
      {toggleBarLocation === 'top' && toggleButton}
      <div
        id={contentId}
        className={collapsiblePanelContentClassNames}
        role="region"
        aria-labelledby={headerButtonId}
        // TODO: remove when isomorphic friendly solution is implemented
        suppressHydrationWarning
        {...testMetadata?.content}
      >
        <div className="sb-collapsible-panel__content-inner">{children}</div>
      </div>

      {toggleBarLocation === 'bottom' && toggleButton}
    </div>
  );
};

export default CollapsiblePanel;
