import { FC, useCallback, useContext, useMemo } from 'react';

// Components
import CollapsiblePanel, { CollapsiblePanelProps } from '../CollapsiblePanel';

// Context
import { AccordionContext } from './Accordion';

// Types
import { TestMetadata } from 'types';

export type Props = Omit<
  CollapsiblePanelProps,
  'onToggleOpen' | 'isOpen' | 'isOpenByDefault'
> & {
  /** ID of the panel, used to control open/close */
  panelId: string;
  // Test IDs for sections of AccordionPanel
  testMetadata?: TestMetadata;
};

const AccordionPanel: FC<Props> = ({
  /** ID of the panel, used to control open/close */
  panelId,
  testMetadata,
  ...otherProps
}) => {
  const { openPanelIds, onPanelToggle } = useContext(AccordionContext);

  const isOpen = useMemo(
    () => openPanelIds.includes(panelId),
    [openPanelIds, panelId],
  );

  const onToggleOpen = useCallback(
    (nextIsOpen: boolean): void => {
      onPanelToggle(panelId, nextIsOpen);
    },
    [panelId, onPanelToggle],
  );

  return (
    <CollapsiblePanel
      isOpen={isOpen}
      onToggleOpen={onToggleOpen}
      {...testMetadata}
      {...otherProps}
    />
  );
};

export default AccordionPanel;
