import { FC } from 'react';

// Components
import { Accordion, AccordionPanel } from '../Accordion';
import CollapsiblePanel from '../CollapsiblePanel';
import MarkdownRenderer from '../MarkdownRenderer';
import Skeleton from '../Skeleton';
import Typography from '../Typography';

// Types
import { FAQGroup, TestMetadata } from '../types';

// Styles
import './FAQ.scss';

export interface FAQProps {
  /** Sets the title on the FAQ */
  title?: string;
  /** Array of release notes to display in modal */
  sectionList?: FAQGroup[];
  /** Sets the is fetching flag */
  isFetching?: boolean;
  /** Sets has fetched flag */
  hasFetched?: boolean;
  // Test IDs for component sections
  testMetadata?: {
    faqEmpty?: TestMetadata;
    faqLoading?: TestMetadata;
  };
  //** markdown content style */
  className?: string;
}

const FAQ: FC<FAQProps> = ({
  title = 'Frequently Asked Questions',
  sectionList = [],
  isFetching = false,
  hasFetched = false,
  testMetadata,
  className,
  ...otherProps
}) => {
  return (
    <div {...otherProps} className="sb-page-container">
      {hasFetched && !sectionList.length ? (
        <div className="sb-faq">
          <div className="sb-empty-faqs" {...testMetadata?.faqEmpty}>
            No FAQs available.
          </div>
        </div>
      ) : (
        <>
          {isFetching ? (
            <div {...testMetadata?.faqLoading}>
              <div className="sb-faq">
                <Typography variant="T80">{title}</Typography>
                <Typography variant="T80">
                  <Skeleton width="80%" />
                </Typography>
                <CollapsiblePanel toggleBar={<Skeleton width="100%" />} />
                <CollapsiblePanel toggleBar={<Skeleton width="100%" />} />
              </div>
            </div>
          ) : (
            <div className="sb-faq">
              <Typography className="sb-title-header" variant="T80">
                {title}
              </Typography>
              {sectionList?.map((faq, i) => (
                <div key={i}>
                  <Typography className="sb-section-header" variant="T60">
                    {faq.title}
                  </Typography>
                  <Accordion className="sb-faq-accordion">
                    {faq.questions.map((qa) => (
                      <AccordionPanel
                        panelId={qa.title}
                        toggleBar={qa.title}
                        key={qa.title}
                      >
                        <MarkdownRenderer
                          className={className}
                          markdown={qa.content}
                        />
                      </AccordionPanel>
                    ))}
                  </Accordion>
                </div>
              ))}
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default FAQ;
