import { createContext, FC, useState, useCallback } from 'react';
import classNames from 'classnames';

// Styles
import './Accordion.scss';

// Types
import { TestMetadata } from 'types';

export interface AccordionProps {
  /** Class name **/
  className?: string;
  /** ID of the default opened panel */
  openByDefault?: string[];
  /** Allow more than one child to be expanded */
  allowMultipleOpen?: boolean;
  /** Called when panels open or close */
  onToggleOpen?: (panelId: string, nextValue: boolean) => void;
  // Test IDs for sections of Accordion
  testMetadata?: TestMetadata;
}

interface AccordionContextProps {
  /** Array of panel IDs that are currently open */
  openPanelIds: string[];
  /** Function that gets called when a panel is toggled */
  onPanelToggle: (panelId: string, nextStateIsOpen: boolean) => void;
}

export const AccordionContext = createContext<AccordionContextProps>({
  openPanelIds: [],
  onPanelToggle: () => {
    // noop
  },
});

const Accordion: FC<AccordionProps> = ({
  children,
  className,
  openByDefault = [],
  allowMultipleOpen = true,
  onToggleOpen = () => void 0,
  testMetadata,
  ...otherProps
}) => {
  const [openPanelIds, setOpenPanelIds] = useState(openByDefault);

  const accordionClassNames = classNames('sb-accordion', className);

  const onPanelToggle = useCallback(
    (panelId: string, nextIsOpen: boolean): void => {
      if (nextIsOpen) {
        if (!allowMultipleOpen) {
          openPanelIds.forEach((id) => onToggleOpen(id, false));
        }
        setOpenPanelIds(
          allowMultipleOpen ? (state) => [...state, panelId] : [panelId],
        );
      } else {
        setOpenPanelIds((state) => state.filter((id) => id !== panelId));
      }
      onToggleOpen(panelId, nextIsOpen);
    },
    [allowMultipleOpen, onToggleOpen, openPanelIds],
  );

  return (
    <AccordionContext.Provider
      value={{ openPanelIds: openPanelIds, onPanelToggle: onPanelToggle }}
    >
      <div {...otherProps} className={accordionClassNames} {...testMetadata}>
        {children}
      </div>
    </AccordionContext.Provider>
  );
};

export default Accordion;
